  const handleNext = () => {
    setActiveStep((prevActiveStep) => prevActiveStep + 1);
  };

  const handleBack = () => {
    setActiveStep((prevActiveStep) => prevActiveStep - 1);
  };

  const handleReset = () => {
    setActiveStep(0);
  };

  function Guardar(){

    const data = {
      TIden,
      Name,
      Apell,
      Genero,
      Mito,
      Efec,
      EPS,
      Vacuna,
      Dosis,
      LoteP,
      FechaDP,
      LoteS,
      FechaDS,
      TipVacunador,
      IPS,
      Regimen,
      Empleo,
      Municipio,
      Barrio,
      Etnia,
      Discap,
      Desplaz,
      AreaR,
      Age,
      dolorIn, eritema, fatiga , mialga , nauseas, adenopatias, diarrea, artralgia, escalofrio, resfriado, fiebre, alergia, cabeza, hinchazon, enrojecimiento, desmayo, problemas, ninguno,cancer, renal, epoc, asma, pulmonar, fibrosis, hipertencion, demencia, diabetes, down, insuficiencia, arteria, miocardio, VIH, inmunodepresion, hepatica, sobrepeso, embarazo, celulas, tabaco, transplante, cerebrovascular, transtorno, ninguno2,covid,inmune,virus,adn,dosDosis,creencia,ningunaCreencia
    }


    console.log(data);
    fetch('http://localhost:5000/', {
      method: 'POST', // *GET, POST, PUT, DELETE, etc.
      mode: 'same-origin', // no-cors, *cors, same-origin
      cache: 'default', // *default, no-cache, reload, force-cache, only-if-cached
      headers: {
        'Content-Type': 'application/json',
        'Access-Control-Allow-Origin':'*',
        'Access-Control-Allow-Methods':'POST'
      },      
      body: JSON.stringify(data),
    })
    .then(data=>{console.log(data)})  
    .catch(err=>{console.log(err)})
    
  }





